export default {
    name: "goldlist",
    data(){
        return{
            list:[]
        }
    },
    mounted:function () {
        let token=JSON.parse(localStorage.getItem('user')).token;
      this.$axios.get('/user/exchanges',
          {
              headers: {
                  'Authorization': 'Bearer ' + token,
              }
          }).then(res=>{
              this.list=res.data.data;
            console.log(res)
      })
    }
}



